import React from 'react';
import styled from 'styled-components';
import { Section, Container } from '@components/global';
import EmailForm from '@ui/emailform';
//import Call2Act from '@ui/Call2Act';
import { StaticQuery, graphql } from 'gatsby';
import Fade from 'react-reveal/Fade';

const ShortChannelling = () => (
  <StaticQuery
    query={graphql`
      query {
        Link2Act: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "link2act" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Fade right>
      <Section id="shortchannelling">
        <Container>
          <HeaderWrapper>
          <Grid>
            <div/>
            <div>
            
              <h4>Subscribe to Us!</h4>
              <p><small>By signing up, you will receive the latest updates and an exclusive article: <b><i>How do hard of hearing people listen to music</i></b>. We will also offer you a secret discount while launching our crowdfunding campaign!</small></p>
              <p><EmailForm accent="inverse"/></p>
              {/*
                <Call2Act text="Sign up to our newsletter" href="https://www.indiegogo.com" accent="inverse" target="_blank"/>
              */}
            </div>
            <div/>
          </Grid>
          </HeaderWrapper>
        </Container>
      </Section>
      </Fade>
    )}
  />
);

export default ShortChannelling;


const HeaderWrapper = styled.div`
  padding: 15px 0 30px 0;
  margin-left: 0%;
  max-width: 100%;
  font-weight: 400;
  vertical-align: middle;
  background-color: ${props => props.theme.color.background.S200}; 
  color: ${props => props.theme.color.grayscale.S1000};
  border: 1px solid  ${props => props.theme.color.background.S300}; 
  border-radius: 1px;
  @media (max-width: ${props => props.theme.screen.md}) {
    background-color: ${props => props.theme.color.background.S100}; 
    max-width: 100%;
    padding: 15px 0 0 0;
    margin-left: 0%;
    margin-bottom: -50px;
    border: none;
    border-top: 1px solid  ${props => props.theme.color.background.S200};
    border-bottom: 1px solid  ${props => props.theme.color.background.S200};  
  }

  ${props =>
    props.accent &&
    `color: ${
      props.accent === 'inverse'
        ? props.theme.color.white.regular
        : props.theme.color.grayscale.S1000
    }`};

  p {
    ${props =>
      props.accent &&
      `color: ${
        props.accent === 'inverse'
          ? props.theme.color.grayscale.S100
          : props.theme.color.grayscale.S1000
      }`};
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 50px;
  text-align: center;
  align-items: top;
  justify-items: center;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    grid-gap: 25px;
    &:last-child {
      margin-bottom: 24px;
    }
  }
`;
